// Mock des constantes
jest.mock('../../../src/utils/constants', () => ({
  LANGUAGE_LEVELS: {
    '1': { title: 'DÃ©butant', color: '#10B981', icon: 'ðŸŒ±' },
    '2': { title: 'Ã‰lÃ©mentaire', color: '#3B82F6', icon: 'ðŸ“š' },
    '3': { title: 'IntermÃ©diaire', color: '#8B5CF6', icon: 'ðŸŽ¯' },
  },
  EXERCISES: {
    vocabulary: { id: 'vocabulary', route: '/tabs/vocabulary' },
    grammar: { id: 'grammar', route: '/tabs/grammar' }
  },
  CONTAINER_SAFE_EDGES: { NO_BOTTOM: [] }
}));

describe('Dashboard - Tests unitaires', () => {
  const mockTheme = {
    colors: {
      background: '#F8FAFC',
      primary: '#3B82F6',
      surface: '#FFFFFF',
      text: '#1F2937',
      textSecondary: '#6B7280'
    }
  };

  const defaultMocks = () => {
    mockUseProgress.mockReturnValue({
      isLoading: false,
      progress: { level1: 50 }
    });
    
    mockUseCurrentLevel.mockReturnValue({
      setCurrentLevel: jest.fn()
    });
  };

  beforeEach(() => {
    jest.clearAllMocks();
    defaultMocks();
  });

  const renderDashboard = (theme = mockTheme) => {
    return render(
      <ThemeContext.Provider value={theme}>
        <Dashboard />
      </ThemeContext.Provider>
    );
  };

  describe('Rendu de base', () => {
    it('devrait rendre tous les composants principaux', () => {
      const { container } = renderDashboard();
      const content = container.firstChild.props.children.toString();
      
      expect(content).toContain('ModernHeader[1][#10B981]');
      expect(content).toContain('HeroContinueSection[none][loading:false]');
      expect(content).toContain('QuickActions[1][#10B981]');
      expect(content).toContain('SimpleMetrics[#10B981]');
      expect(content).toContain('LearningProgress[25%][1]');
      expect(content).toContain('RevisionOrchestrator[1]');
    });

    it('devrait afficher le loading', () => {
      mockUseProgress.mockReturnValue({ isLoading: true, progress: {} });
      
      const { getByText } = renderDashboard();
      expect(getByText('Chargement du tableau de bord...')).toBeTruthy();
    });

    it('devrait fonctionner sans contexte theme', () => {
      const { container } = render(<Dashboard />);
      expect(container.firstChild).toBeTruthy();
    });
  });

  describe('Props des composants', () => {
    it('devrait passer les bonnes props Ã  ModernHeader', () => {
      const { container } = renderDashboard();
      const content = container.firstChild.props.children.toString();
      expect(content).toContain('ModernHeader[1][#10B981]');
    });

    it('devrait passer les bonnes props Ã  LearningProgress', () => {
      const { container } = renderDashboard();
      const content = container.firstChild.props.children.toString();
      expect(content).toContain('LearningProgress[25%][1]'); // progression du niveau 1
    });

    it('devrait passer isLoading Ã  HeroContinueSection', () => {
      const useLastActivity = require('../../../src/hooks/useLastActivity').default;
      useLastActivity.mockReturnValue({
        lastActivity: null,
        isLoading: true,
        reload: jest.fn()
      });

      const { container } = renderDashboard();
      const content = container.firstChild.props.children.toString();
      expect(content).toContain('HeroContinueSection[none][loading:true]');
    });
  });

  describe('Gestion des niveaux', () => {
    it('devrait utiliser le niveau du hook useDashboardLevel', () => {
      const { useDashboardLevel } = require('../../../src/screens/Dashboard/hooks/useDashboardLevel');
      useDashboardLevel.mockReturnValue({
        currentLevel: '3',
        handleChangeActiveLevel: jest.fn(),
        levelColor: '#8B5CF6'
      });

      const { container } = renderDashboard();
      const content = container.firstChild.props.children.toString();
      
      expect(content).toContain('ModernHeader[3][#8B5CF6]');
      expect(content).toContain('QuickActions[3][#8B5CF6]');
      expect(content).toContain('RevisionOrchestrator[3]');
    });

    it('devrait calculer la progression des niveaux', () => {
      const useRealTimeProgress = require('../../../src/hooks/useRealTimeProgress').default;
      useRealTimeProgress.mockReturnValue({
        getLevelProgress: jest.fn((level) => {
          const progress = { '1': 30, '2': 60, '3': 90 };
          return progress[level] || 0;
        }),
        refresh: jest.fn()
      });

      const { container } = renderDashboard();
      const content = container.firstChild.props.children.toString();
      expect(content).toContain('LearningProgress[30%][1]'); // niveau 1 = 30%
    });
  });

  describe('Gestion des activitÃ©s', () => {
    it('devrait passer lastActivity Ã  HeroContinueSection', () => {
      const useLastActivity = require('../../../src/hooks/useLastActivity').default;
      useLastActivity.mockReturnValue({
        lastActivity: { type: 'vocabulary', level: '2' },
        isLoading: false,
        reload: jest.fn()
      });

      const { container } = renderDashboard();
      const content = container.firstChild.props.children.toString();
      expect(content).toContain('HeroContinueSection[vocabulary][loading:false]');
    });
  });

  describe('Gestion des erreurs', () => {
    it('devrait gÃ©rer l\'absence de progress', () => {
      mockUseProgress.mockReturnValue({ isLoading: false });
      
      const { container } = renderDashboard();
      expect(container.firstChild).toBeTruthy();
    });

    it('devrait gÃ©rer l\'absence de currentLevel', () => {
      mockUseCurrentLevel.mockReturnValue({});
      
      const { container } = renderDashboard();
      expect(container.firstChild).toBeTruthy();
    });
  });

  describe('Couleurs et thÃ¨me', () => {
    it('devrait utiliser les couleurs par dÃ©faut sans thÃ¨me', () => {
      const { container } = render(<Dashboard />);
      expect(container.firstChild).toBeTruthy();
    });

    it('devrait appliquer les couleurs du niveau courant', () => {
      const { useDashboardLevel } = require('../../../src/screens/Dashboard/hooks/useDashboardLevel');
      useDashboardLevel.mockReturnValue({
        currentLevel: '2',
        handleChangeActiveLevel: jest.fn(),
        levelColor: '#3B82F6'
      });

      const { container } = renderDashboard();
      const content = container.firstChild.props.children.toString();
      expect(content).toContain('#3B82F6');
    });
  });
});